body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .square {
    background: blue;
    border: 4px solid lime;
    color: lime;
    float: left;
    font-size: 26px;
    font-weight: bold;
    line-height: 3px;
    height: 65px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 65px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
  .bold {
      font-weight: bold;
  }
  .red {
      color: red;
  }

  html{
      background-color: gray;
  }


